const swiperCard = (() => {
  let init = false
  let swiper
  return () => {
    if (window.innerWidth <= 768) {
      if (!init) {
        init = true
        swiper = new Swiper('.swiper', {
          loop: true,
          slidesPerView: 'auto',
          spaceBetween: 25,
          pagination: {
            el: '.swiper-pagination',
          },
        })
      }
    } else if (init) {
      if (swiper && typeof swiper.destroy === 'function') {
        swiper.destroy(true, true)
        swiper = undefined
      }
      init = false
    }
  }
})()
swiperCard()
window.addEventListener('resize', swiperCard)
